import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function Square(props) {
    return (
        <button className={`square ${props.selected ? 'blink' : ''}`}
            onClick={props.onClick}>
            {props.value}
        </button>
    );
}

class Board extends React.Component {
    renderSquare(i, j) {
        return (
            <Square
                selected={(this.props.selected.x === i)
                    && (this.props.selected.y === j) ? true : false}
                key={i + ' ' + j}
                value={this.props.squares[i][j]}
                onClick={() => this.props.onClick(i, j)}
            />);
    }

    buildBoard() {
        let rows = [];
        for (let i = 0; i < 16; i++) {
            let cols = [];
            for (let j = 0; j < 16; j++) {
                cols.push(this.renderSquare(i, j));
            }
            rows.push(<div className='board-row' key={i}>{cols}</div>);
        }
        return rows;
    }

    render() {
        return (
            <div>{this.buildBoard()}</div>
        );
    }
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            prevSquares: null,
            squares: initGame(),
            canRevert: false,
            xIsNext: true,
            selectedSquare: { x: null, y: null }
        }
    }

    handleClick(i, j) {
        var current = this.state.squares.map((arr) => arr.slice());
        /*Check winner to return early*/
        if (calculateWinner(current)) {
            return;
        }
        if (this.state.selectedSquare.x !== null) {/*Pawn selected, handle landing step*/
            if (current[i][j] === '路') {
                //clear valid move indicators
                this.clearDots(current);
                current[this.state.selectedSquare.x][this.state.selectedSquare.y] = null;
                current[i][j] = this.state.xIsNext ? 'X' : 'O';
                this.setState((prev) => ({
                    squares: current,
                    xIsNext: !prev.xIsNext,
                    canRevert: true,
                    selectedSquare: { x: null, y: null }
                }));
            } else if (current[i][j] === current[this.state.selectedSquare.x][this.state.selectedSquare.y]) {
                this.clearDots(current);
                this.handleInitialSelect(i, j, current);
            }
            return;
        } else {/*Pawn not selected, handle selecting step*/
            this.handleInitialSelect(i, j, current);
        }
    }

    clearDots(current) {
        for (let i in current) {
            for (let j in current[i]) {
                if (current[i][j] === '路') {
                    current[i][j] = null;
                }
            }
        }
    }

    handleInitialSelect(i, j, current) {
        if ((this.state.xIsNext && current[i][j] === 'X')
            || (!this.state.xIsNext && current[i][j] === 'O')) {
            this.setState({
                selectedSquare: { x: i, y: j },
                canRevert: false
            }, () => this.getValidMoves(i, j, current));
        }
        return;
    }

    revert() {
        if (!this.state.canRevert) {
            alert('Unable to revert');
            return;
        }
        this.setState((prevState) => ({
            squares: prevState.prevSquares,
            xIsNext: !prevState.xIsNext,
            canRevert: false
        }));
    }

    getValidMoves(i, j, current) {
        let prevState = current.map((arr) => arr.slice());
        //add single move
        for (let a of [-1, 0, 1]) {
            for (let b of [-1, 0, 1]) {
                if (i + a >= 0 && j + b >= 0 && i + a < 16 && j + b < 16 &&
                    !(a === b && a === 0) && !current[i + a][j + b]) {
                    current[i + a][j + b] = '路';
                }
            }
        }
        //add jumps
        let stack = [[i, j]];
        let seen = new Set();
        while (stack.length) {
            let cur = stack.pop();
            let curStr = cur[0] + ' ' + cur[1];
            if (seen.has(curStr)) {
                continue;
            }
            seen.add(cur[0] + ' ' + cur[1]);
            //look around 2 squares away for jump
            for (let a of [-1, 0, 1]) {
                for (let b of [-1, 0, 1]) {
                    if (cur[0] + a >= 0 && cur[1] + b >= 0 && cur[0] + a < 16 && cur[1] + b < 16 &&
                        !(a === b && a === 0) && (current[cur[0] + a][cur[1] + b] === 'X'
                            || current[cur[0] + a][cur[1] + b] === 'O')) {
                        let new_i = cur[0] + 2 * a;
                        let new_j = cur[1] + 2 * b;
                        if (new_i >= 0 && new_j >= 0 && new_i < 16 && new_j < 16 &&
                            !seen.has(new_i + ' ' + new_j) && !current[new_i][new_j]) {
                            current[new_i][new_j] = '路';
                            stack.push([new_i, new_j]);
                        }
                    }
                }
            }
        }
        this.setState({
            prevSquares: prevState,
            squares: current
        });
    }

    render() {
        const current = this.state.squares;
        const winner = calculateWinner(current);

        let status;
        if (winner) {
            status = 'Winner: ' + winner;
        } else {
            status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
        }

        return (
            <div className='game'>
                <div className='game-board'>
                    <Board
                        selected={this.state.selectedSquare}
                        squares={current}
                        onClick={(i, j) => this.handleClick(i, j)}
                    />
                </div>
                <div className='game-info'>
                    <div>{status}</div>
                    <button onClick={() => this.revert()}>Revert!</button>
                </div>
            </div>
        );
    }
}

function initGame() {
    let squares = Array(16).fill().map(() => Array(16).fill(null));
    let pawnSet = [[0, 0], [0, 1], [0, 2], [0, 3], [0, 4],
    [1, 0], [1, 1], [1, 2], [1, 3], [1, 4], [2, 0], [2, 1],
    [2, 2], [2, 3], [3, 0], [3, 1], [3, 2], [4, 0], [4, 1]];
    pawnSet.forEach((element) => {
        squares[element[0]][element[1]] = 'O';
        squares[15 - element[0]][15 - element[1]] = 'X';
    });
    return squares;
}

function calculateWinner(squares) {
    let checkZone = [[0, 0], [0, 1], [0, 2], [0, 3], [0, 4],
    [1, 0], [1, 1], [1, 2], [1, 3], [1, 4], [2, 0], [2, 1],
    [2, 2], [2, 3], [3, 0], [3, 1], [3, 2], [4, 0], [4, 1]];
    let xWins = true;
    let oWins = true;
    checkZone.forEach((element) => {
        if (squares[element[0]][element[1]] !== 'X') {
            xWins = false;
        }
        if (squares[15 - element[0]][15 - element[1]] !== 'O') {
            oWins = false;
        }
    });
    if (xWins) {
        return 'X';
    } else if (oWins) {
        return 'O';
    } else {
        return;
    }
}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);